import React, { useRef, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { NavLink, Link } from 'react-router-dom';
import cl from "./SidebarMenu.module.scss";
import { useSelector } from 'react-redux';


import M from "materialize-css/dist/js/materialize.min.js";

import SettingsSvg from './icons/SettingsSvg.jsx';

function SidebarMenu() {
    const sidenav_element = useRef(null);

    useEffect(() => {

        let sc = sidenav_element.current

        if (sc) {
            M.Sidenav.init(sc, {})
        }
    }, [sidenav_element])

    const closeSidebar = () => {
        let s = document.getElementsByClassName("sidenav-overlay")

        for (let x = 0; x < s.length; x++) { s[x].click() }
    }

    const { t } = useTranslation()

    const isUserAuthenticated = useSelector(state => state.auth.isAuthenticated)

    const user = useSelector(state => state.auth.user)

    const userIsAdmin = user?.is_staff

    const open_settings = () => {
        closeSidebar()
    }

    return (
        <div
            ref={sidenav_element}
            id="slide-out"
            className={`sidenav ${cl.sidebarMenu_mainWrapper} sidemenu`}
        >
            <div className={cl.content}>
                <div>
                    <div className={`${cl.top} top`}>
                        {!isUserAuthenticated ?
                            /* */
                            <div className={cl.top_main__user_is_not_authenticated}>
                                <Link className={`${cl.auth_btn} auth_btn`} to="/users/login" onClick={closeSidebar}>
                                    {t('main_content.menu.top_content.login', "login")}
                                </Link>
                                <Link className={`${cl.auth_btn} auth_btn`} to="/users/register" onClick={closeSidebar}>
                                    {t('main_content.menu.top_content.register', "register")}
                                </Link>
                            </div>
                            /* */
                            :
                            /* */
                            <div className={cl.top_main__user_is_authenticated}>
                                <div className={cl.left}>
                                    <div className={cl.photo_wrapper_main}>
                                        <div className={cl.photo_wrapper}>

                                            <img
                                                className={cl.photo}
                                                src={user.photo}
                                                alt={(user.first_name).charAt(0).toUpperCase()}
                                            />


                                        </div>
                                    </div>
                                    <span className={cl.username}>
                                        Hello,&nbsp;{user.first_name}
                                    </span>
                                </div>
                                <div className={cl.right}>
                                    <Link to="settings" onClick={open_settings} className={`${cl.settings} settings`}>
                                        <span className={cl.icon}>
                                            <SettingsSvg />
                                        </span>
                                    </Link>
                                </div>
                            </div>
                            /* */
                        }
                    </div>
                    <div className={cl.main}>
                        <div className={cl.nav_links_wrapper}>

                            {[
                                [`${t('main_content.menu.main_page', "main page.")}`, "/"],
                                [`${t('main_content.menu.about_me_page', "about me page")}`, "about-me"],
                                [`${t('main_content.menu.contact_me_page', "contact me page")}`, "contact-me"],
                            ].map(l =>
                                <NavLink
                                    key={l[1]}
                                    to={l[1]}
                                    className={
                                        (navData) => navData.isActive ? `${cl.navLink} ${cl.active} navLink active` : `${cl.navLink} navLink`
                                    }
                                    onClick={closeSidebar}
                                >
                                    {l[0]}
                                </NavLink>
                            )}

                        </div>
                    </div>
                </div>

                {!userIsAdmin &&
                    <div onClick={closeSidebar} className={`${cl.bottom} bottom`}>
                        <Link to="admin" className={`${cl.admin_link} admin_link`}>
                            Admin
                        </Link>
                    </div>
                }

            </div>
        </div>
    );
}

export default SidebarMenu;